import React, { useState, useEffect } from "react";
import Header from "../Header";
import Footer from "../Footer";
import { Link } from "react-router-dom";
import useHttp from "../../hooks/hooks.http";
import useAuth from "../../hooks/hooks.auth";

export default function Registration() {
  const validateEmail = (email) => {
    return String(email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
  };
  useEffect(() => {
    document.title = "Регистрация";
  }, []);
  const [form, setForm] = useState({
    first_name: "",
    last_name: "",
    phone: "",
    email: "",
    password: "",
    password_check: "",
  });
  const { login } = useAuth();
  const { request } = useHttp();
  const formChangeHandler = (event) => {
    if (event.target.value === "") event.target.classList.remove("error");
    if (event.target.name === "password_check") {
      event.target.value === form.password
        ? event.target.classList.remove("error")
        : event.target.classList.add("error");
    } else if (event.target.name === "email") {
      validateEmail(event.target.value)
        ? event.target.classList.remove("error")
        : event.target.classList.add("error");
    }
    setForm((prev) => {
      prev[event.target.name] = event.target.value;
      return prev;
    });
  };
  const registerClickHandler = async (event) => {
    for (const formKey in form) {
      if (form[formKey] === "") {
        return;
      }
    }
    try {
      const data = await request(
        "/api/auth/register/",
        "POST",
        {
          ...form,
          info: {
            address: "",
            phone: form.phone,
          },
        },
        {
          "Content-Type": "application/json",
        }
      );
      await login(data.access, data.id);
      window.location.replace("/account/info");
    } catch (e) {}
  };
  return (
    <>
      <Header />
      <main className="registration-main">
        <section className="page__auth auth">
          <div className="auth__wrapper">
            <div className="auth__image">
              <img src={require("../../img/Auth/bg.png")} alt="" />
            </div>
            <div className="auth__form-box">
              <div className="auth__form auth-form">
                <h1 className="auth-form__title">Регистрация в Mama Organic</h1>
                <div className="auth-form__subtitle">
                  <p>
                    Amet minim mollit non deserunt ullamco est sit aliqua dolor
                    do amet sint. Velit officia consequat duis enim velit
                    mollit.
                  </p>
                </div>
                <div className="auth-form__inputs">
                  <div className="auth-form__input-box">
                    <label htmlFor="" className="auth-form__input-label">
                      Имя
                    </label>
                    <input
                      type="text"
                      name={"first_name"}
                      onInput={formChangeHandler}
                      placeholder="Введите ваше имя"
                      className="auth-form__input"
                    />
                  </div>
                  <div className="auth-form__input-box">
                    <label htmlFor="" className="auth-form__input-label">
                      Фамилия
                    </label>
                    <input
                      type="text"
                      name={"last_name"}
                      onInput={formChangeHandler}
                      placeholder="Введите ваше имя"
                      className="auth-form__input"
                    />
                  </div>
                  <div className="auth-form__input-box">
                    <label htmlFor="" className="auth-form__input-label">
                      Email
                    </label>
                    <input
                      type="text"
                      onInput={formChangeHandler}
                      name={"email"}
                      placeholder="Введите ваш email"
                      className="auth-form__input"
                    />
                  </div>
                  <div className="auth-form__input-box">
                    <label htmlFor="" className="auth-form__input-label">
                      Телефон
                    </label>
                    <input
                      type="text"
                      onInput={formChangeHandler}
                      name={"phone"}
                      placeholder="Введите телефон"
                      className="auth-form__input"
                    />
                  </div>
                  <div className="auth-form__input-box">
                    <label htmlFor="" className="auth-form__input-label">
                      Пароль
                    </label>
                    <input
                      type="password"
                      onInput={formChangeHandler}
                      name={"password"}
                      placeholder="Введите пароль"
                      className="auth-form__input"
                    />
                  </div>
                  <div className="auth-form__input-box">
                    <label htmlFor="" className="auth-form__input-label">
                      Повторите пароль
                    </label>
                    <input
                      type="password"
                      onInput={formChangeHandler}
                      name={"password_check"}
                      placeholder="Введите пароль"
                      className="auth-form__input"
                    />
                  </div>
                </div>
                <button
                  onClick={registerClickHandler}
                  className="auth-form__submit"
                >
                  Зарегистрироваться
                </button>
                <Link to={"/login"} className="auth-form__link">
                  У меня уже есть аккаунт
                </Link>
              </div>
            </div>
          </div>
        </section>
      </main>
      <Footer />
    </>
  );
}
