import React, { useState, useEffect, useContext } from "react";
import Header from "../Header";
import Footer from "../Footer";
import CheckoutForm from "./CheckoutForm";
import { HeaderContext } from "../../context/HeaderContext";
import CheckoutProducts from "./CheckoutProducts";

export default function Checkout() {
  const [products, setProducts] = useState([]);
  const [totalPrice, setTotalPrice] = useState("0");
  const { get, removeById, storageLength } = useContext(HeaderContext);
  useEffect(() => {
    const prods = get();
    setProducts(prods);
    document.title = "Корзина";
  }, []);

  useEffect(() => {
    if (products !== null) {
      let newPrice = 0;
      for (const product of products) {
        newPrice += product.price * product.count;
      }
      setTotalPrice(newPrice.toLocaleString("en").replaceAll(",", " "));
    }
  }, [products]);

  useEffect(() => {
    const prods = get();
    setProducts(prods);
  }, [storageLength]);

  return (
    <>
      <Header />
      <main className="checkout-main">
        <div className="checkout__hf-container">
          <CheckoutForm totalPrice={totalPrice} products={products} />
          <CheckoutProducts products={products} />
        </div>
      </main>
      <Footer />
    </>
  );
}
